import React from 'react'
import cocina from './cocinaData'
import { Link } from 'react-router-dom'

const Section4 = () => {
  const language = 'es'
  const name = {
    es: 'Nuestra Cocina',
    cat: '',
    it: 'Our Cuisine'
  }
  return (
    <div className='section4'>
      <div className='fan' />
      <div className='name '>{name[language]}</div>
      <div className='container'>
        {cocina.map(({ name, description }, index) => (
          <div key={index} className={'cocina' + index}>
            <div className='title'>{name[language]}</div>
            <div className='description'>{description[language]}</div>
          </div>
        ))}
        <img alt='restaurant' />
      </div>
      <div className='button'>
        <Link to='/menu'>
          <div className='button-bg'>
            Ver Menus
          </div>
        </Link>
      </div>
      <div className='fan2' />
    </div>
  )
}

export default Section4